<script>
import { onMounted, onUnmounted } from 'vue'
import { useRouter } from 'vue-router'

export default {
  name: 'LandingPage',
  setup() {
    const router = useRouter()
    let observer = null
    let ticking = false
    
    const handleStartShopping = () => {
      const isLoggedIn = !!localStorage.getItem('access')
      if (isLoggedIn) {
        router.push('/products')
      } else {
        router.push('/login')
      }
    }
    
    const handleCreateAccount = () => {
      router.push('/register')
    }
    
    const handleBrowseProducts = () => {
      const isLoggedIn = !!localStorage.getItem('access')
      if (isLoggedIn) {
        router.push('/products')
      } else {
        router.push('/login')
      }
    }
    
    const handleCategoryClick = () => {
      const isLoggedIn = !!localStorage.getItem('access')
      if (isLoggedIn) {
        router.push('/products')
      } else {
        router.push('/login')
      }
    }
    
    const handleImageError = (event) => {
      const img = event.target
      const alt = img.alt || 'Image'
      
      // Create a proper placeholder based on the original dimensions
      let width = 200, height = 200
      
      if (img.src.includes('height=50')) {
        width = height = 50
      } else if (img.src.includes('height=40')) {
        width = height = 40
      } else if (img.src.includes('width=300')) {
        width = 300
        height = 200
      }
      
      // Create SVG placeholder
      const svgContent = `
        <svg width="${width}" height="${height}" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 ${width} ${height}">
          <defs>
            <linearGradient id="grad" x1="0%" y1="0%" x2="100%" y2="100%">
              <stop offset="0%" style="stop-color:#006884;stop-opacity:1" />
              <stop offset="100%" style="stop-color:#97BCC7;stop-opacity:1" />
            </linearGradient>
          </defs>
          <rect width="100%" height="100%" fill="url(#grad)"/>
          <text x="50%" y="50%" text-anchor="middle" dy=".3em" fill="white" font-family="Arial, sans-serif" font-size="${Math.min(width, height) / 8}" font-weight="500">
            ${alt}
          </text>
        </svg>
      `
      
      img.src = `data:image/svg+xml;base64,${btoa(svgContent)}`
      img.style.objectFit = 'cover'
    }
    
    const scrollToFeatures = () => {
      const featuresSection = document.getElementById('features')
      if (featuresSection) {
        featuresSection.scrollIntoView({ 
          behavior: 'smooth',
          block: 'start'
        })
      }
    }
    
    const animateStats = () => {
      const statNumbers = document.querySelectorAll('.stat-number')
      
      statNumbers.forEach(stat => {
        const target = parseInt(stat.getAttribute('data-target'))
        const increment = target / 50 // Reduced iterations for better performance
        let current = 0
        
        const updateStat = () => {
          if (current < target) {
            current += increment
            stat.textContent = Math.ceil(current)
            if (!ticking) {
              requestAnimationFrame(updateStat)
              ticking = true
              setTimeout(() => { ticking = false }, 16) // 60fps throttle
            }
          } else {
            stat.textContent = target
          }
        }
        
        updateStat()
      })
    }
    
    const setupIntersectionObserver = () => {
      observer = new IntersectionObserver((entries) => {
        entries.forEach(entry => {
          if (entry.isIntersecting) {
            entry.target.classList.add('animate-in')
            
            // Trigger stats animation when stats section is visible
            if (entry.target.classList.contains('stats-section')) {
              setTimeout(animateStats, 200) // Slight delay for better UX
            }
          }
        })
      }, {
        threshold: 0.1,
        rootMargin: '0px 0px -50px 0px'
      })
      
      // Observe all sections
      const sections = document.querySelectorAll('section')
      sections.forEach(section => {
        observer.observe(section)
      })
    }
    
    onMounted(() => {
      // Delay observer setup to improve initial load performance
      setTimeout(setupIntersectionObserver, 100)
    })
    
    onUnmounted(() => {
      if (observer) {
        observer.disconnect()
      }
    })
    
    return {
      handleStartShopping,
      handleCreateAccount,
      handleBrowseProducts,
      handleCategoryClick,
      handleImageError,
      scrollToFeatures
    }
  }
}
</script>

